<!-- 歌曲搜索组件 -->
<template>
    <div class="search-comp" :style="{height: wrapHeight + 'px'}">
        <div>
            <div style="margin-bottom:10px;text-align:center;">
                <img src="../../../static/images/music.icon.png" width="100" alt="">
            </div>
            <input type="text" placeholder="请输入搜索关键字" v-model="query" @keyup.enter="queryHandle"/>
            <span><input type="button" value="搜索" @click="queryHandle"/></span>
        </div>
        <div class="search-comp-footer">
            云中白鹤 @ {{new Date().toLocaleDateString().replace(/\//g,'-')}} &copy; 悅听
        </div>
    </div>
</template>

<script>
    export default {
        name: "Search",
        data(){
            return {
                query: '',
                isQueryed: false
            }
        },
        beforeRouteEnter: function(to, from, next){
            to.meta.keepAlive = true;
            next();
        },
        methods: {
            async queryHandle(){
                if(!this.query.length){
                    return false;
                }
                this.isQueryed = true;
                let data = await $.ajax({
                    url: `${this.HOME}from=webapp_music&method=baidu.ting.search.catalogSug&format=json&query=${this.query}`,
                    type: 'get',
                    cache: false,
                    dataType: 'jsonp',
                });
                let {song = []} = data;
                if(!data){
                    console.log('%c 搜索歌曲失败', 'color:#ff0000');
                }
                song.map(item => {
                    item['song_id'] = item['songid'];
                    item['title'] = item['songname'];
                    item["author"] = item["artistname"];
                    return item;
                });
                this.$store.commit('setSongList', song);
                this.$router.push({
                    name: 'home',
                    params: {
                        pagination: false
                    }
                });
                this.query = '';
                this.isQueryed = false;
            }
        },
        beforeRouteLeave: function(to, from, next){
            // 设置下一个路由的 meta
            to.meta.keepAlive = !this.isQueryed;
            next();
        },
        computed: {
            wrapHeight(){
                return `${document.body.offsetHeight - 45 - 40}`;
            }
        }
    }
</script>

<style scoped>
    .search-comp {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .search-comp input {
        padding: 9px 7px;
        width: 480px;
        height: 40px;
        outline: none;
        border: 1px solid #B8B8B8;
    }

    .search-comp input,
    .search-comp span {
        float: left;
    }

    .search-comp input:focus {
        border-color: #3388FF;
    }

    .search-comp span {
        width: 102px;
        height: 40px;
        border: 1px solid #38f;
        border-bottom: 1px solid #2e7ae5;
        background-color: #38f;
    }

    .search-comp span:hover {
        border-bottom: 1px solid #2771d9;
    }

    .search-comp span input {
        cursor: pointer;
        width: 102px;
        height: 38px;
        line-height: 38px;
        padding: 0;
        border: 0;
        background: none;
        background-color: #38f;
        font-size: 16px;
        color: white;
        box-shadow: none;
        font-weight: normal;
        outline: none;
    }

    .search-comp span input:hover {
        height: 39px;
        _height: 38px;
        background-color: #317ef3;
        border-bottom: 1px solid #2868c8;
        box-shadow: 1px 1px 1px #ccc;
    }

    .search-comp-footer {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        line-height: 40px;
        color: #fff;
        text-align: center;
        letter-spacing: 1px;
        background-color: #404453;
    }
</style>


